import React from 'react'
import "../LoginRegister/sass/loginStyle.scss"
import {NavLink}  from 'react-router-dom'
import {Carousel} from 'react-bootstrap'
// import Images from '../Images/carousel/Food.jpeg'

export default function Home() {
    const [index, setIndex] = React.useState(0);
    const handleSelect = (selectedIndex, e) => {
        setIndex(selectedIndex);
    };
    return (
        <div className='root-container'>
            <Carousel style={{width:"75%",maxHeight:"100%"}} activeIndex={index} onSelect={handleSelect}>
                <Carousel.Item>
                    <img s
                        className="d-block w-100"
                        src={require('../../Images/carousel/Food.jpeg')}//"/800x400?text=First slide&bg=373940"
                        alt="First slide"
                    />
                    {/* <Carousel.Caption>
                    <h3>First slide label</h3>
                    <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
                    </Carousel.Caption> */}
                </Carousel.Item>
                <Carousel.Item>
                    <img
                        className="d-block w-100"
                        src={require('../../Images/carousel/Stationary.jpeg')}//"holder.js/800x400?text=Second slide&bg=282c34"
                        alt="Second slide"
                    />

                    {/* <Carousel.Caption>
                    <h3>Second slide label</h3>
                    <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
                    </Carousel.Caption> */}
                </Carousel.Item>
                <Carousel.Item>
                    <img 
                        className="d-block w-100"
                        src={require('../../Images/carousel/Electronics.jpeg')}
                        alt="Third slide"
                    />

                    {/* <Carousel.Caption>
                    <h3>Third slide label</h3>
                    <p>
                        Praesent commodo cursus magna, vel scelerisque nisl consectetur.
                    </p>
                    </Carousel.Caption> */}
                </Carousel.Item>
            </Carousel>
            <br/>
            <span style={{fontSize:"35px",color:"white"}}>Click <span style={{color:"blue"}}>Products</span> button to view All Products</span>
            <NavLink exact activeClassName="active"  to={'/Product'} > <button className="btn btn-primary">Products</button></NavLink>
        </div>
    )
}
